<template>
  <div id="me" ref="me">
    <head-top title="我的"></head-top>
    <div class="top">
      <span>个人信息</span>
      <div class="tit3">
        <img src="../../assets/me/head_portrait@3x.png" alt="" ref="headPic" @click="modify">
        <span :class="{disabl: !loginStatus}">{{name}}</span>
      </div>
    </div>
    <ul class="menu">
      <li @click="jumpRoute('nikeName')">
        <div class="left">
          <span>昵称</span>
        </div>
        <div class="right">
          <span v-if="loginStatus">{{name}}</span>
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
      <li @click="jumpRoute('phone')">
        <div class="left">
          <span>手机号</span>
        </div>
        <div class="right">
          <span v-if="loginStatus" @click="countBind('phone')">{{phoneBind}}</span>
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
      <li @click="jumpRoute('wechat')">
        <div class="left">
          <span>微信</span>
        </div>
        <div class="right">
          <span v-if="loginStatus" @click="countBind('wechat')">{{wechatBind}}</span>
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
      <li @click="jumpRoute('alipay')">
        <div class="left">
          <span>支付宝</span>
        </div>
        <div class="right">
          <span v-if="loginStatus" @click="countBind('alipay')">{{alipayBind}}</span>
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
      <li @click="jumpRoute('aboutUs')">
        <div class="left">
          <img src="../../assets/me/about_us@3x.png" alt="">
          <span>关于我们</span>
        </div>
        <div class="right">
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
      <li @click="jumpRoute('suggest')">
        <div class="left">
          <img src="../../assets/me/feedback@3x.png" alt="">
          <span>意见反馈</span>
        </div>
        <div class="right">
          <img src="../../assets/global/into@3x.png" alt="">
        </div>
      </li>
    </ul>
    <div class="button" @click="login" v-if="loginStatus">
      <img src="../../assets/me/sign_out@3x.png" alt="">
      <span>{{loginState}}</span>
    </div>
    <!-- 修改头像途径 -->
    <actionSheet v-show="1" setHeight="200">
      <div slot="main">
        <ul class="changePic">
          <li>Change pic</li>
          <li @click="actionMode(1)">Take a photo</li>
          <li @click="actionMode(2)">Select from the phone album</li>
          <li @click="actionMode(3)">Cancel</li>
        </ul>
      </div>
    </actionSheet>
    <!-- 退出登录 -->
    <messageBox setHeight="150" setType="confirm" v-show="messageBoxType === 'loginOut'" >
        <div slot="confirmColumn" class="common_alert">
            <div class="alert_icon">
              <span>退出登录</span>
            </div>
            <div class="alert_describe">确定要退出登录吗？</div>
            <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)" >取消</div>
            <div class="determine_one ok" @click="alertChoose(1)">确定</div>
            </div>
        </div>
    </messageBox>
    <messageBox setHeight="150" setType="confirm" v-show="messageBoxType === 'unbindWechat'" >
        <div slot="confirmColumn" class="common_alert">
            <div class="alert_describe unbind">确定要解除微信绑定吗？</div>
            <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)" >取消</div>
            <div class="determine_one ok" @click="alertChoose(2)">确定</div>
            </div>
        </div>
    </messageBox>
    <messageBox setHeight="150" setType="confirm" v-show="messageBoxType === 'unbindAlipay'" >
        <div slot="confirmColumn" class="common_alert">
            <div class="alert_describe unbind">确定要解除支付宝绑定吗？</div>
            <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)" >取消</div>
            <div class="determine_one ok" @click="alertChoose(3)">确定</div>
            </div>
        </div>
    </messageBox>
    <loading></loading>
    <transition mode="in-out" name="slide-left">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import headTop from '../../components/common/head'
import messageBox from '../../components/common/messageBox'
import actionSheet from '../../components/common/actionSheet'
import loading from '../../components/Loading/index'
import {mapActions, mapGetters} from 'vuex'
import {
  isLoginRequest,
  localPhotoRequest,
  takePhotoRequest,
  logoutUserRequest,
  bindingAlipayRequest,
  bindingWeChatRequest,
  untiedAlipayRequest,
  untiedWechatRequest,
  userInformationRequest

} from '../../api/method'
import {
  isLoginResponse,
  switchNetworkResponse,
  networkStatusResponse,
  userInformationResponse,
  modifyAvatarSendedResponse,
  modifyUserInformationResponse,
  logoutUserResponse,
  untiedWechatResponse,
  untiedAlipayResponse
} from '../../api/getData'
import { getStore } from '../../utils/index'
export default{
  data () {
    return {
      active: 'me',
      loginState: '退出登录',
      name: '未登录',
      headPic_status: require('../../assets/me/head_portrait@3x.png'),
      wechatBind: '立即绑定',
      alipayBind: '立即绑定',
      phoneBind: '立即绑定'
    }
  },
  components: {
    'head-top': headTop,
    messageBox,
    actionSheet,
    loading

  },
  computed: {
    ...mapGetters(['messageType', 'loginStatus', 'getActionsheet', 'maqttState']),
    messageBoxType () {
      return this.messageType
    },
    actionSheet () {
      return this.getActionsheet
    }
  },
  created () {
    

     if (!this.loginStatus) {
      isLoginRequest(this.NAVIGATOR, this)
    }
  },
  mounted () {

  // this.$router.push({path:'device/temperature'})
    this.$nextTick(() => {
      this.$refs.me.style.height = document.documentElement.clientHeight
    })
    isLoginResponse(this.NAVIGATOR, this)
    switchNetworkResponse(this.NAVIGATOR, this)
    userInformationResponse(this.NAVIGATOR, this,'me')
    networkStatusResponse(this.NAVIGATOR, this)
    modifyAvatarSendedResponse(this.NAVIGATOR, this)
    modifyUserInformationResponse(this.NAVIGATOR, this)
    logoutUserResponse(this.NAVIGATOR, this)
    untiedWechatResponse(this.NAVIGATOR, this)
    untiedAlipayResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      window.isLoginResponse(true)
      window.userInformationResponse(true, '{"nickName":"木心จุ๊บ","headPic":"http://thirdwx.qlogo.cn/mmopen/vi_32/CTuzr6ITibKU2jBVibmptZZcRkCj9Nh7d4tCWun4ah1B1ic1H2jWHnEgrriaQIriaGrkptRsCNKibBh6LEGAdC3fCWbg/132","isHavePwd":false,"mobile":"1322222222","thirdInfos":[{"nickName":"木心จุ๊บ","type":10}]}')
    }
    if (this.loginStatus) {
      let localUserInfo = JSON.parse(getStore('Userinfo'))
      if (!localUserInfo) return false
      this.name = localUserInfo.nickName
      console.log(this.$refs.headPic)
      this.$refs.headPic.src = localUserInfo.headPic
      this.loginState = '退出登录'
      if (localUserInfo.mobile !== '' && localUserInfo.mobile !== undefined) {
        this.phoneBind = '已绑定'
      }
      localUserInfo.thirdInfos.forEach(ele => {
        if (ele.type === 10) {
          console.log('微信绑定')
          this.wechatBind = '已绑定'
        } else if (ele.type === 11) {
          console.log('支付宝绑定')
          this.alipayBind = '已绑定'
        }
      })
    } else {
      this.name = '未登录'
    }
    // let localLoginInfo = JSON.parse(getStore('loginInfo'))
    // if (localLoginInfo) {
    //   switch (localLoginInfo.type) {
    //     case 'wechat':
    //     case 'alipay':
    //       break
    //     case 'phone':
    //       if (this.loginStatus) {
    //         this.name = localLoginInfo.phone
    //       }
    //       if (localLoginInfo.bind) {
    //         this.phoneBind = '已绑定'
    //       }
    //       break
    //     default:
    //       break
    //   }
    // }
    // if (this.loginStatus) {
    //   this.loginState = '退出登录'
    //   let localLogininfo = JSON.parse(getStore('loginInfo'))
    //   if (localLogininfo) {
    //     this.name = localLogininfo.phone
    //   } else {
    //     this.name = '你的昵称'
    //   }
    // }
  },
  methods: {
    ...mapActions(['showMessagesBox', 'showActionsheet', 'showLoading']),
    alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({'type': 'null'})
          break
        case 1: // 退出登录
          this.showMessagesBox({'type': 'null'})
          logoutUserRequest(this.NAVIGATOR, this)
          if (this.$root.ISDEBUG) {
            window.logoutUserResponse(true)
          }
          break
        case 2: // 微信解绑
          untiedWechatRequest(this.NAVIGATOR, this)
          this.showMessagesBox({'type': 'null'})
          this.showLoading(true)
          break
        case 3: // 支付宝解绑
          untiedAlipayRequest(this.NAVIGATOR, this)
          this.showMessagesBox({'type': 'null'})
          this.showLoading(true)
          break
        default:
          break
      }
    },
    login () {
      if (this.loginStatus) {
        console.log('messglogout1')
        this.showMessagesBox({'type': 'loginOut'})
      } else {
        this.$router.push({path: '/login'})
      }
    },
    modify () {
      if (!this.loginStatus) {
        this.$router.push({path: '/login'})
        return false
      }
      this.showActionsheet()
    },
    actionMode (num) {
      switch (num) {
        case 1:
          takePhotoRequest(this.NAVIGATOR, this)
          this.showActionsheet()
          break
        case 2:
          localPhotoRequest(this.NAVIGATOR, this)
          this.showActionsheet()
          break
        case 3:
          this.showActionsheet()
          break
        default:
          break
      }
    },
    jumpRoute (type) {
      if (!this.loginStatus) {
        this.$router.push({path: '/login'})
        return false
      }
      switch (type) {
        case 'nikeName':
          this.$router.push({path: '/Home/me/changename'})
          break
        case 'phone':
          if (this.phoneBind === '已绑定') {
            this.$router.push({name: 'phonebind', params: {type: 'unbind'}})
          } else {
            this.$router.push({name: 'phonebind', params: {type: 'bind'}})
          }
          break
        case 'wechat':
          break
        case 'alipay':
          break
        case 'aboutUs':
          this.$router.push({path: '/Home/me/aboutUs'})
          break
        case 'suggest':
          this.$router.push({path: '/Home/me/suggestion'})
          break
        default:
          break
      }
    },
    countBind (type) {
      switch (type) {
        case 'wechat':
          if (this.wechatBind === '已绑定') {
            this.showMessagesBox({'type': 'unbindWechat'})
          } else {
            bindingWeChatRequest(this.NAVIGATOR, this)
          }
          break
        case 'alipay':
          if (this.alipayBind === '已绑定') {
            this.showMessagesBox({'type': 'unbindAlipay'})
          } else {
            bindingAlipayRequest(this.NAVIGATOR, this)
          }
          break
        case 'phone':
          // bindMobilePhoneRequest(this.NAVIGATOR, this)
          break
        default:
          break
      }
    }
  },
  watch: {
    $route (to, from) {
      if (from.name === 'phonebind') {
        let localLoginInfo = JSON.parse(getStore('loginInfo'))
        if (localLoginInfo.bind) {
          // this.phoneBind = '已绑定'
          this.$set(this, 'phoneBind', '已绑定')
          console.log(this.phoneBind)
        }
      } else if (from.name === 'changename') {
        let localLoginInfo = JSON.parse(getStore('Userinfo'))
        this.name = localLoginInfo.nickName
      }
    },
    loginStatus (newval) {
      if (newval) {
        console.log('ssss')
        userInformationRequest(this.NAVIGATOR, this)
      }
    }
  }
}
</script>

<style scoped lang="less">
#me{
  .wh(100%, 100%);
  .display-flex;
  flex-direction: column;
  color: #222222;
  background-Color: #d1d1d1;
  .top{
    // .display-flex;
    height: 225px;
    width: 100%;
    // flex-direction: column;
    background: white;
    padding: 17px 30px 0 30px;
    &>span{
    text-align: left;
    font-size: 34px;
    font-weight: 600;
    display: block;
    }
    .tit3{
      margin-top: 40px;
      display: flex;
      span{
        align-self: center;
        font-size: 28px;
        margin-left: 14px;
      }
      img{
        .wh(98px, 97px);
        border-radius: 50%;
        display: block;
        align-self: center;
        line-height: 38px;
      }
      .disabl{
        .wh-l(132px, 56px);
        background: #d2d2d2;
        border-radius: 10px;
      }
    }
  }
  .menu{
    .wh(100%, auto);
    // .display-flex;
    // flex-direction: column;
    border-bottom: .5px solid #e6e5e5;
    flex: 1;
    margin-top: 18px;
    background: white;
    li{
      .wh(100%, 115px);
      .display-flex;
      justify-content: space-between;
      padding: 0 30px;
      border-top: .5px solid #e6e5e5;
      // &:last-child{
        // border-bottom: .5px solid #e6e5e5;
      // }
    }
    .left{
      height: 100%;
      .display-flex;
      img{
        .wh(44px, 44px);
        align-self: center;
        margin-right: 18px;
      }
    }
    .right{
      flex: 1;
      display: flex;
      justify-content: flex-end;
      // .wh(15px, 28px);
      align-self: center;
      img{
        .wh(14px, 24px);
        align-self: center;
      }
      span{
        align-self: center;
        margin-right: 18px;
      }
    }
    span{
      font-size: 30px;
      align-self: centr;
      line-height: 130px;
    }
  }
  .button{
    margin: 0 30px;
    .wh-l(92%,92px);
    font-size: 30px;
    background: @themeColor;
    color: white;
    position: absolute;
    bottom: 130px;
    flex: 1;
    display: flex;
    justify-content: center;
    img{
      .wh(38px, 38px);
      align-self: center;
      margin-right: 17px;
    }
  }
  .common_alert{
    /deep/.determine_one{
      .wh-l(50%, 76px);
      margin: 0 52px 28px 78px;
      background: #aaaaaa;
      border-radius: 10px;
    }
    .ok{
      background: @themeColor;
    }
    .alert_icon{
      margin-top: 41px;
    }
    .alert_describe{
      margin-top: 45px;
      color: #303030;
    }
    .unbind{
      margin-top: 80px;
      font-size: 35px;
    }
  }
  .changePic{
    .wh(100%,auto);
    li{
      .wh-l(100%,100px);
      font-size: 31px;
      color: #575757;
      border-bottom: .5px solid #eeeeee;
    }
    li:nth-child(4){
        border-bottom: 0;
    }
    li:nth-child(1) {
      font-weight: 900;
    }
  }
}
</style>



